
import Swiper from 'vux/src/components/swiper/swiper.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'


import scrollTop from '@/components/scrollTop.vue'

const json = {code: 'b4'}
const json02 = {
order: 'ASC',
pageNum: 1,
pageSize: 24
}
export default {
components: {
Swiper,
LoadMore,
scrollTop
},
data () {
return {
errorImg: 'this.src="' + require('../../assets/images/common/errorImg1.jpg') + '"',
pid: '',
banners: [],
lists: [],
loading: false,
isLastPage: false
}
},
watch: {
'$route': function (to, from) {
let pid = this.$route.params.pid / 1
this.pid = pid
console.log('watch')
this.reset()
this.loadMore()
}
},
created () {
this.pid = this.$route.params.pid / 1
this.reset()
this.getBanners()
this.loadMore()
},
methods: {
async getBanners () {
let result = await this.axios.post('/web/adlist', json)
let data = result.data
let lists = []
data.map((item, index) => {
if (item.status) {
lists.push({
url: item.linkurl,
img: this.base_img + item.img,
title1: item.title
})
}
})
this.banners = lists
},
async getProducts (cid) { // 加载产品
console.log('getProducts', cid)
let _this = this
if (cid / 1 === 1) {
cid = ''
}
json02.cid = cid
let result = await this.axios.post('/web/newlist', json02)
let data = result.data
console.log(data)
if (this.isLastPage) { return }
if (data.isLastPage) {
this.isLastPage = true
}
let lists = data.list
lists = lists.filter((item, index) => {


item.pic = { // vue-lazyload
src: item.pic,
loading: require('../../assets/images/loading.gif'),
error: require('../../assets/images/common/errorImg1.jpg')
}
item.date = _this.GLOBAL.timestampToTime(item.createDate)
return item.status / 1
})
if (json02.pageNum / 1 === 1) {
this.lists = lists
} else {
this.lists = this.lists.concat(lists)
}
this.loading = false
json02.pageNum++
},
loadMore () { // 上拉加载更多
if (this.isLastPage) {
return
}
this.loading = true
this.getProducts(this.pid)
},
reset () {
this.lists = []
json02.pageNum = 1
this.isLastPage = false
},
link (item, index) { // 产品详情跳转
let target = this.lists[index]
this.$router.push({name: 'newDetail', params: {'id': target.id}})
}
}
}



